import React, { Component } from 'react';
import './PortfolioSchool.css';



class PortfolioSchool extends Component {
  render() {
    return (
      <div className="PortfolioSchool">
        <div className="PortfolioSchool-container">
          <img className="PortfolioSchool-background" src={this.props.portfolioOngoing.backgroundLarge} alt={this.props.portfolioOngoing.title}></img>
          <div className="PortfolioSchool-logoContainer">
            <img className="PortfolioSchool-logo" src={this.props.portfolioOngoing.icon} alt={this.props.portfolioOngoing.title}></img>
            <img className="PortfolioSchool-background-mobile" src={this.props.portfolioOngoing.background} alt={this.props.portfolioOngoing.title}></img>
          </div>
          <div className="PortfolioSchool-textContainer">
            <p style={{ color: this.props.colorTitle }} className="PortfolioSchool-title">
              {this.props.portfolioOngoing.title}
            </p>
            <p style={{ color: this.props.color }} className="PortfolioSchool-description"> {this.props.portfolioOngoing.description}</p>
          </div>
        </div>
      </div>
    );
  }
}

export default PortfolioSchool;